    box-sizing: border-box;
  }
  
  * {
    margin: 0;
    font-family: "Geist Mono", "Josefin Sans", ubuntu, sans-serif;
  }
  
  html {
    height: 100vh;
  }

  body {
    height: 100%;
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
  }
  
  img, picture, video, canvas, svg {
    display: block;
    max-width: 100%;
  }
  
  input, button, textarea, select {
    font: inherit;
  }
  
  p, h1, h2, h3, h4, h5, h6 {
    overflow-wrap: break-word;
  }
  
  p {
    text-wrap: pretty;
  }
  h1, h2, h3, h4, h5, h6 {
    text-wrap: balance;
  }
  
  #root, #__next {
    isolation: isolate;
  }

body > header {
    height: 15%;
    width: 100%;
    font-size: 2.5rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 1rem;
    background-color: #cff0f1;
}

main {
    height: 85%;
    display: grid;
    grid-template-columns: 1fr 4fr;
    grid-template-rows: 1fr;
}

nav {
    display: flex;
    flex-direction: column;
    padding: 1.5rem;
    background-color: #f7f7f7;
    border-right: 1px solid #bebbbb;
    font-size: 1.5rem;
    gap: 1rem;
    overflow: hidden;
}

nav > header, nav > div > header, div.new-project {
    font-weight: 500;
}

div.add-button {
  position: fixed;
  right: 1.5rem;
  bottom: 1.5rem;
  font-weight: 500;
  border-radius: 50%;
  height: 3rem;
  width: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #cff0f1;
  cursor: pointer;
}

div.add-button > svg {
  height: 2rem;
}

div.projects > div {
    padding: 0.5rem;
    border-radius: 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.home {
  padding: 0.5rem;
  border-radius: 5px;
}

div.projects > div > div {
  font-size: 1.25rem;
  font-weight: 500;
  border-radius: 50%;
  height: 2.5rem;
  width: 2.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #cff0f1;
}

div.projects > div:hover, .home:hover {
    background-color: #bebbbb;
}

div.projects {
    display: flex;
    flex-direction: column;
    overflow: scroll;
    height: 55vh;
}

div.new-project {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

div.new-project > svg {
  height: 2rem;
}

::-webkit-scrollbar {
  display: none;
}

div#content {
  padding: 2rem;
}

div.container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

div.container > div {
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0.75rem;
  gap: 0.75rem;
  border-radius: 5px;
  background-color: #e4e4e4;
}

.low {
  border-left: 5px solid #73aa5e;
}

.medium {
  border-left: 5px solid #ddb340;
}

.high {
  border-left: 5px solid #ad6464;
}

div.container > div > .title {
  position: absolute;
  left: 0.75rem;
}

div.container > div > .details {
  font-size: 0.8rem;
  padding: 0.35rem 0.7rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  border: 1px solid gray;
  font-weight: 550;
}

div.container > div > .details:hover {
  background-color: #bebbbb;
}

div.container > div > div > svg, div.container > div > svg {
  height: 1.25rem;
}

div.container > div > svg:hover {
  fill: #73869e;
}

div.add-project-modal {
  padding: 2rem;
  border: 2px solid black;
  background-color: white;
  border-radius: 5px;
  width: 50%;
  height: 40%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: none;
  grid-template-rows: auto 1fr auto;
  gap: 1rem;
}

div.add-project-modal > div.title {
  font-size: 2rem;
  display: flex;
  justify-content: space-between;
}

div.add-project-modal > button {
  align-self: center;
  justify-self: flex-end;
  font-size: 0.75rem;
  padding: 0.5rem;
  font-weight: 600;
  background-color: white;
  color: #73aa5e;
  border: 1px solid #73aa5e;
  border-radius: 3px;
  display: flex;
  align-items: center;
  transition: all .3s;
}

div.add-project-modal > div.title > svg {
  height: 2rem;
}

div.add-project-modal > input {
  border: none;
  outline: none;
}

.create-project-button:hover {
  background-color: #73aa5e;
  color: white;
}

div.add-todo-modal {
  padding: 2rem;
  border: 2px solid black;
  background-color: white;
  border-radius: 5px;
  width: 50%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: none;
  grid-template-rows: auto 1fr auto;
  gap: 1rem;
}

div.add-todo-modal > div.title {
  font-size: 2rem;
  display: flex;
  justify-content: space-between;
}

div.title > svg {
  height: 2rem;
}

div.add-todo-modal > input {
  outline: none;
  border: none;
}

#todo-desc {
  height: 150px;
  resize: none;
  border: none;
  outline: none;
}

div.date-container {
  display: flex;
}

div.priority-container {
  display: flex;
  align-items: center;
}

div.button-container {
  display: flex;
}

.priority-button {
  display: flex;
  align-items: center;
  margin-right: 0.75rem;
  padding: 0.25rem 0.8rem;
  border-radius: 3px;
  font-size: 0.85rem;
  font-weight: 600;
  cursor: pointer;
  transition: all .3s;
}

input[type="radio"] {
  position: relative;
  left: 2.2rem;
  height: 1px;
  width: 1px;
  opacity: 0;
}

.low-priority-button {
  border: 1px solid #73aa5e;
  color: #73aa5e;
}

.low-priority-button-active {
  color: white;
  background-color: #73aa5e;
}

.medium-priority-button {
  border: 1px solid #ddb340;
  color: #ddb340;
}

.medium-priority-button-active {
  color: white;
  background-color: #ddb340;
}

.high-priority-button {
  border: 1px solid #ad6464;
  color: #ad6464;
}

.high-priority-button-active {
  color: white;
  background-color: #ad6464;
}

input[type="date"] {
  border: 1px solid #9cb0c9;
  text-transform: uppercase;
  font-weight: 550;
  color: #9cb0c9;
  border-radius: 3px;
}

div.options-container {
  display: grid;
  row-gap: 1rem;
  grid-template-columns: 1fr 3fr 2fr;
  justify-items: flex-start;
  align-items: center;
}

button.create-todo-button {
  grid-column: 3/4;
  grid-row: 1/3;
  justify-self: end;
  align-self: flex-end;
  font-size: 0.8rem;
  display: flex;
  align-items: center;
  padding: 0.25rem 0.8rem;
  border-radius: 3px;
  font-size: 0.85rem;
  font-weight: 600;
  cursor: pointer;
  border: 1px solid #73aa5e;
  color: #73aa5e;
  background-color: white;
  transition: all .3s;
}

button.create-todo-button:hover {
  background-color: #73aa5e;
  color: white;
}

div.details-modal {
  padding: 2rem;
  border: 2px solid black;
  background-color: white;
  border-radius: 5px;
  width: 50%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: none;
  gap: 1rem;
}

div.details-modal > div.title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

div.details-modal > svg {
  height: 2rem;
  position: absolute;
  right: 1.25rem;
  top: 1.25rem;
